import moment from 'moment';
import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { DATES, isEmpty, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Vinculo } from '../../../../../models/dtos/mfp/vinculo';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { AbonoPecuniarioRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './abonoPecuniarioRV';

const DEFAULT_VALUES: AbonoPecuniarioRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  vinculosSelecionados: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  dtFinalLabel: 'Final período aquisitivo',
  dtFinalRequired: true,
  dtInicialLabel: 'Inicio período aquisitivo',
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
};

const AbonoPecuniarioReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AbonoPecuniarioRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = () => {
    const abonoPecuniarioRV: AbonoPecuniarioRV = formProps.getValues();
    const errors = [];

    if (
      (!isEmpty(abonoPecuniarioRV.dtInicial) && isEmpty(abonoPecuniarioRV.dtFinal)) ||
      (!isEmpty(abonoPecuniarioRV.dtFinal) && isEmpty(abonoPecuniarioRV.dtInicial))
    ) {
      errors.push('Ambas as datas do período aquisitivo são obrigatórias, caso deseje informá-las.');
    }

    if (!isEmpty(abonoPecuniarioRV.dtInicial) && !isEmpty(abonoPecuniarioRV.dtFinal)) {
      abonoPecuniarioRV.vinculosSelecionados.forEach((vinculo: Vinculo) => {
        if (moment(abonoPecuniarioRV.dtInicial, 'DD/MM/YYYY').isBefore(moment(vinculo.dataAdmissao, 'DD/MM/YYYY'))) {
          errors.push(
            'O período informado para o Abono Pecuniário para o vínculo ' +
              `${vinculo.matricula}` +
              ' - ' +
              `${vinculo.trabalhador.nome}` +
              ' é anterior à Data de Admissão'
          );
        }
      });
    }
    return errors;
  };

  return (
    <WindowComponent
      title="Solicitação de Férias em Abono Pecuniário"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={5124}
      wikiTitle={'Relatório - Abono Pecuniário'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
        extraConfigOpened={true}
      >
        <VinculoIS
          id="vinculosSelecionados"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhadores"
          isPrependLabel={true}
          placeholder="Selecione o Trabalhador(es)"
          inputSearchProps={{ selectProps: { isMulti: true } }}
          apenasEstagiarios={true}
        />

        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Termo"
          searchViewProps={{ apenasAbonoPecuniario: true }}
          apenasAbonoPecuniario={true}
          placeholder="Selecione o Termo"
          id="termo"
          isPrependLabel={true}
          inputSearchProps={{ bzSize: 'xl' }}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default AbonoPecuniarioReportView;
